.Employ {
  padding: 60px 0;
    .container{
        .wrapper{
          width: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          align-items: center;
          min-height: 100px;
          border: 1px solid black;
          .title{

          }
        }
    }
}


.container {
  composes: container from "../Header/Header.module.scss";
}

@media (min-width: 360px) and (max-width: 600px){
  .Employ{
    padding: 20px 0;
    .container{
      .wrapper{
        min-height: 60vh;
        justify-content: center;
      }
      .title{
        text-align: center;
        font-size: 2.2rem;
        padding: 20px;
        margin-bottom: 20px;
      }
    }
  }
}